// lib/hooks/use-auth.ts
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { authService, type LoginCredentials, type RegisterData } from '@/lib/api/auth.service'
import { useAuthStore } from '@/lib/store/auth-store'
import { useRouter } from 'next/navigation'
import { toast } from 'sonner'

export const useAuth = () => {
  const { setUser, logout: storeLogout } = useAuthStore()
  const router = useRouter()
  const queryClient = useQueryClient()

  // Query pour rÃ©cupÃ©rer l'utilisateur actuel
  const { data: user, isLoading, isError, refetch } = useQuery({
    queryKey: ['currentUser'],
    queryFn: authService.getCurrentUser,
    retry: false,
    enabled: !!localStorage.getItem('access_token'),
    staleTime: 5 * 60 * 1000, // 5 minutes
  })

  // Mutation de connexion
  const loginMutation = useMutation({
    mutationFn: authService.login,
    onSuccess: (userData) => {
      console.log('âœ… Connexion rÃ©ussie, mise Ã  jour du store:', userData)
      setUser(userData)
      queryClient.setQueryData(['currentUser'], userData)
      toast.success('Connexion rÃ©ussie !')
      
      // Invalider la query des Ã©quipes pour les recharger
      queryClient.invalidateQueries({ queryKey: ['teams'] })
      
      router.push('/dashboard')
    },
    onError: (error: any) => {
      console.error('âŒ Erreur de connexion:', error)
      const message = error.response?.data?.detail || error.response?.data?.error || 'Identifiants incorrects'
      toast.error(message)
    },
  })

  // Mutation d'inscription
  const registerMutation = useMutation({
    mutationFn: authService.register,
    onSuccess: () => {
      toast.success('Inscription rÃ©ussie ! VÃ©rifiez votre email.')
      router.push('/login')
    },
    onError: (error: any) => {
      const message = error.response?.data?.email?.[0] || 'Erreur lors de l\'inscription'
      toast.error(message)
    },
  })

  // Fonction de dÃ©connexion
  const logout = async () => {
    try {
      await authService.logout()
      storeLogout()
      queryClient.clear()
      toast.success('DÃ©connexion rÃ©ussie')
      router.push('/login')
    } catch (error) {
      console.error('Erreur lors de la dÃ©connexion:', error)
    }
  }

  return {
    user,
    isLoading,
    isError,
    login: loginMutation.mutate,
    register: registerMutation.mutate,
    logout,
    refetch,
    isLoggingIn: loginMutation.isPending,
    isRegistering: registerMutation.isPending,
  }
}