'use client'

import { useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { useAuthStore } from '@/lib/store/auth-store'
import { useAuth } from '@/lib/hooks/use-auth'
import { Sidebar } from '@/components/layout/sidebar'
import { Loader2 } from 'lucide-react'

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const router = useRouter()
  const { isAuthenticated } = useAuthStore()
  const { user, isLoading } = useAuth()

  useEffect(() => {
    // Si pas de token et pas en chargement, rediriger vers login
    if (!isLoading && !isAuthenticated && typeof window !== 'undefined') {
      const token = localStorage.getItem('access_token')
      if (!token) {
        console.log('ðŸ”’ Aucun token trouvÃ©, redirection vers /login')
        router.push('/login')
      }
    }
  }, [isAuthenticated, isLoading, router])

  // Afficher un loader pendant la vÃ©rification
  if (isLoading) {
    return (
      <div className="flex h-screen items-center justify-center">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    )
  }

  // Si pas authentifiÃ©, ne rien afficher (la redirection est en cours)
  if (!isAuthenticated || !user) {
    return null
  }

  return (
    <div className="flex h-screen overflow-hidden">
      <Sidebar />
      <main className="flex-1 overflow-y-auto bg-slate-50">
        {children}
      </main>
    </div>
  )
}